.App{
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background: #15181d;
}
.App .title{
  font-size: 20px;
  font-weight: 800;
  padding: 16px;
  text-transform: capitalize;
  background: #383e47d9;
  color: white;
  text-align: center;
}
.App .user-page{
  height: -webkit-fill-available;
  width: -webkit-fill-available; 
}
.App .card-container{
  height: 100%;
width: calc(100% - 40px);
}
.App .user-page .card-wrapper {
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: baseline;
  overflow-y: auto;
  position: relative;
  padding: 16px;
}
.App .user-page .card-wrapper .card-outer {
  display: flex;
  background: white;
  padding: 16px;
  border-radius: 8px;
  width: fit-content;
  text-align: center;
  height: 100px;
  margin-right: 16px;
  margin-bottom: 16px;
  align-items: center;
}
.App .user-page .card-wrapper .card-outer .info-profile {
  padding: 8px;
  border-radius: 50%;
  background: darkolivegreen;
  height: 26px;
  font-size: 20px;   
  text-transform: uppercase;
  width: 26px;
  margin-right: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.App .user-page .card-wrapper .card-outer  .info-title {
  font-size: 16px;
  text-align: left;
  font-weight: 600; 
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;  
  overflow: hidden;
}
.App .user-page .card-wrapper .card-outer .info-title {
  font-size: 16px;
  text-align: left;
  font-weight: 600;
}
.App .user-page .card-wrapper .card-outer span.info-dec {
  font-weight: 400;
}
.refresh-btn-wrapper:hover {

  background: #86abd180;
}
.refresh-btn-wrapper {
  position: absolute;
  right: 16px;
  top: 16px;
  border-radius: 50%;
  padding: 8px;
  display: flex;
  align-items: center;
}
.refresh-btn-wrapper.rotate {
  animation: rotation 2s infinite linear;
}
@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
.refresh-btn-wrapper img{
  width: 24px;
  height: 24px;
}
/* medial query  */
@media screen and (max-width:375px) {
  
  .App .user-page .card-wrapper {

    flex-direction: column;
  }
}
